import { Injectable, Input, OnInit, Component } from '@angular/core';
import { Http, Response } from '@angular/http';
import { Headers, RequestOptions } from '@angular/http';
import 'rxjs/add/operator/map';

@Injectable()
export class LeadDataService{

  //private apiURL = 'https://reala-api.herokuapp.com/api/leads';
  private apiURL;// = 'http://localhost:8000/api';
  private headers: Headers = new Headers();
  private token: string;

  constructor(private http: Http) {
    var url = window.location.href;
    if (url[url.length - 6] == ':'){
      url = "http://localhost:8000/"
    }
    this.apiURL = url + 'api';
  }

  setToken(token){
    this.token = token;
    this.headers.append('Content-Type', 'application/x-www-form-urlencoded');
    this.headers.append('Authorization', 'Bearer ' + token);
  }

  getToken(){
    return this.token
  }

  getLeads(filter) {
    console.log(this.token)
    console.log("Requesting Leads...")

    if (filter == '?'){
      filter = '';
    }
    return this.http.get(this.apiURL + '/leads/' + filter, {
      headers: this.headers,
    })
      .map(response => response.json());
  }

  getLead(id) {
    return this.http.get(this.apiURL + '/owners/' + id + '/')
      .map(response => response.json());
  }

  getEvents(owner_id) {
    return this.http.get(this.apiURL + '/events?owner=' + owner_id)
      .map(response => response.json());
  }

  setEvent(data) {
    let headers = new Headers({ 'Content-Type': 'application/json' });
    let options = new RequestOptions({ headers: headers });

    return this.http.post(this.apiURL + '/events/', data, options)
      .map(response => response.json());
  }

}



