import {Component, Input, Output, EventEmitter} from '@angular/core';
import {LoginService} from './login.service';
import {Observable} from 'rxjs/Observable';
import 'rxjs/add/operator/catch';
import {LeadDataService} from "./leaddata.service";
declare var $: any;

@Component({
  selector: 'login',
  templateUrl: './login.component.html',
  providers: [LoginService, LeadDataService],
})

export class Login {

  @Output() toggleLeads: EventEmitter<string> = new EventEmitter<string>();

  private token: string;
  private username: string = '';
  private password: string = '';

  constructor(public loginService: LoginService, public leadDataService: LeadDataService) {
  }

  setToken(data) {
    console.log("Setting Token...")
    this.token = data['access_token'];

    localStorage.setItem('current_token', JSON.stringify({token: this.token}));
    this.leadDataService.setToken(data['access_token'])
    this.toggleLeads.emit(data['access_token']);
    return this.token
  }

  delToken(){
    this.token = null;
    localStorage.removeItem('current_token');
    this.leadDataService.setToken(null);
    return this.token
  }

  toggleVisibility() {
    $('#login_form').css('display', 'none')
  }

  deactivateForm() {
    //$('#submit_button').addClass('disabled');
    $('#username').prop('disabled', true)
    $('#password').prop('disabled', true)
    $('#login_loader').css('display', 'block');
  }

  activateForm() {
    $('#submit_button').removeClass('disabled');
    $('#username').prop('disabled', false)
    $('#password').prop('disabled', false)
    $('#login_loader').css('display', 'none');
  }

  getToken() {
    var that = this;
    this.deactivateForm()
    this.loginService.login(this.username, this.password)
      .catch(err => {
        this.activateForm();
        return Observable.throw(err);
      })
      .map(response => response.json())
      .subscribe(
        data => this.setToken(data),
        err => function () {
          this.activateForm();
          console.error(err);
        },
        {} = function(){
          //that.toggleLeads.emit('on');
        }
      )
  }

  loseToken() {
    this.loginService.logout(this.token)
      .catch(err => {
        console.log("Error trying to revoke token on server.");
        return Observable.throw(err);
      })
      .map(response => response.json())
      .subscribe(
        data => data,
        err => function () {
          console.error(err);
        },
      );
  }

  login(){
    console.log("Logging in...")
    this.getToken();
  }

  logout(){
    console.log("Logging out...")
    this.loseToken();
    this.delToken();
    console.log("Removing leads...")
    this.toggleLeads.emit(null);
    this.toggleVisibility();
  }

}



