import { Component, OnInit, ViewChild } from '@angular/core';
import { LeadDataService } from './leaddata.service';
import { Login } from './login.component'

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css'],
  providers: [LeadDataService],
})

export class AppComponent implements OnInit{
  // Lead Container
  leadsData: any[] = [];
  token: string;
  filters: string = '?';

  @ViewChild(Login) login: Login;

  toggleLeads(token: string):void {
    if (token != null) {
      this.getLeadsOnInit(token);
    } else {
      this.leadsData = [];
    }
  }

  constructor(private leadDataService: LeadDataService) {
  }

  urlParam(name){
    var results = new RegExp('[\?&]' + name + '=([^&#]*)').exec(window.location.href);

    if (results != null){
      return results[1] || 0;
    } else{
      return null;
    }
  }

  getLeadsOnInit(token){
    console.log("Getting Leads...")
    this.leadDataService.setToken(token)
    this.leadDataService.getLeads(this.filters).subscribe(
      data => this.populateLeads(data.results),
      err => console.error(err)
    )
  }

  getLeads(){
    console.log("Getting Leads...")
    this.leadDataService.getLeads(this.filters).subscribe(
      data => this.populateLeads(data.results),
      err => console.error(err)
    )
  }

  populateLeads(leads_data){
    for (var i = 0; i < leads_data.length; ++i) {
      this.leadsData.push(leads_data[i]);
    }
  }

  ngOnInit(){
    var currentToken = JSON.parse(localStorage.getItem('current_token'));
    if (this.urlParam('postal_code') != null){
      this.filters += 'postal_code=' + this.urlParam('postal_code');
    }
    if (currentToken != null){
      this.login.toggleVisibility();
      this.leadDataService.setToken(currentToken['token'])
      this.getLeads();
    }
  }
}



