import {Component, Input, AfterViewInit, OnInit} from '@angular/core';
import {LeadDataService} from './leaddata.service';
declare var $: any;

@Component({
  selector: 'lead',
  templateUrl: './lead.component.html',
  styleUrls: ['./app.component.css'],
  providers: [LeadDataService],
})
export class Lead {

  @Input()
  leadData: any[] = [];

  id: number;
  first_name: string = '';
  last_name: string = '';
  email_address: string = '';
  phone_number: string = '';


  select_claimed: boolean = false;
  select_called: boolean = false;
  select_mailed: boolean = false;
  select_social: boolean = false;
  select_other: boolean = false;
  notes: string = '';

  formatted_address: string = '';
  events: any[] = [];

  constructor(public leadDataService: LeadDataService) {
  }

  getLead() {
    this.leadDataService.getLead(this.id).subscribe(
      data => this.consume_detail_json(data),
      err => console.error(err),
      () => this.getEvents()
    );
  }

  getEvents() {
    var i;
    this.leadDataService.getEvents(this.id).subscribe(
      data => {
        for (i = 0; i < data['results'].length; i++){
          this.addEvent(data['results'][i])
        }
      },
      err => console.error(err),
    )
  }

  open_modal() {
    // TODO: Fix loading time
    $('#modal_' + this.id).modal('open');
    this.getLead();
  }

  ngOnInit() {
    this.consume_json(this.leadData)
  }

  ngAfterViewInit() {
    $('.modal').modal({
      ready: function (modal, trigger) {
      }
    });
    $('.tooltipped').tooltip();
  }

  consume_json(d) {
    console.log(d);
    this.id = d['id'];
    this.first_name = d['first_name'];
    this.last_name = d['last_name'];

    this.formatted_address = d['home']['formatted_address'];

    for (var i = 0; i < d['events'].length; i++) {
      this.events.push(d['events'][i]);
    }
  }

  consume_detail_json(d) {
    this.id = d['id'];
    this.first_name = d['first_name'];
    this.last_name = d['last_name'];
    this.email_address = d['email_address'];
    this.phone_number = d['phone_number'];

    this.events = [];
    for (var i = 0; i < d['events'].length; i++) {
      this.events.push(d['events'][i]);
    }
  }

  /*
   Event Stuff vvv
   */

  icon_urls = [
    'http://icons.iconarchive.com/icons/jeanette-foshee/simpsons-03/32/Townspeople-Hans-Moleman-icon.png',
    'http://icons.iconarchive.com/icons/jeanette-foshee/simpsons-05/32/Homertopia-Cool-Homer-icon.png',
    'http://icons.iconarchive.com/icons/jeanette-foshee/simpsons-04/32/Guest-Stars-Spinal-Tap-Rock-star-Bart-icon.png',
    'http://icons.iconarchive.com/icons/jeanette-foshee/simpsons-06/32/Homertopia-Homers-Woohoo-icon.png',
    'http://icons.iconarchive.com/icons/jeanette-foshee/simpsons-08/32/Marge-O-Rama-Marge-in-curlers-icon.png',
  ];

  get_type(event) {
    return event['type'];
  }

  get_notes(event) {
    return event['notes'];
  }

  get_tags(event) {
    var tag_list: any[] = [];
    if (event['claimed']) { tag_list.push('Claimed')}
    if (event['called']) { tag_list.push('Called')}
    if (event['mailed']) { tag_list.push('Mailed')}
    if (event['social']) { tag_list.push('Social')}
    if (event['other']) { tag_list.push('Other')}
    return tag_list
  }

  get_occurred(event) {
    var date = new Date(event['occurred']);
    var now = new Date();

    // If different month or year
    if ( now.getMonth() != date.getMonth() || now.getFullYear() != date.getFullYear()){
      return date.getMonth() + 1 + '/' + date.getDate() + '/' + date.getFullYear();
    }
    // Same month/year, different day
    if (now.getDate() - date.getDate() > 0) { //&& now.getDate() - date.getDate() < 0) {
      return date.getMonth() + 1 + '/' + date.getDate() + '/' + date.getFullYear();
    }
    var time_sig = ' AM';
    var hour = date.getHours();
    if (hour > 12) {
      hour = hour % 12;
      time_sig = ' PM';
    }

    var padding = '';
    if (('' + date.getMinutes()).length == 1){
      padding = '0';
    }
    return hour + ':' + date.getMinutes() + padding + time_sig;
  }

  get_icon_url(event) {
    if (event.icon_url == null) {
      event.icon_url = this.icon_urls[Math.floor(Math.random() * this.icon_urls.length)];
    }
    return event.icon_url;
  }

  addEvent(event) {
    for (var i = 0; i < this.events.length; i++) {
      if (this.events[i]['id'] == event['id']) {
        console.log("Lead Already Present")
        console.log(event)
        this.events[i] = event
        return event
      }
    }
    this.events.unshift(event)
    return event
  }

  keyFunction(event) {
    if (event.key == "Enter") {
      if (this.notes == ''){
        return
      }
      var data = {
        'owner': this.id,
        'notes': this.notes,
        "claimed": this.select_claimed,
        "called": this.select_called,
        "mailed": this.select_mailed,
        "social": this.select_social,
        "other": this.select_other
      };

      this.leadDataService.setEvent(data).subscribe(
        data => this.addEvent(data),
        err => console.error(data)
      )
    }
  }
}



